import Carte from './carte'
import styles from './pageCartes.module.css'
import csharp from '../resources/c-sharp.png'
import react from '../resources/atom.png'
import css from '../resources/css.png'
import html from '../resources/html.png'
import java from '../resources/java.png'
import js from '../resources/js.png'
import nodejs from '../resources/nodejs.png'
import sql from '../resources/database.png'


export default function pageCartes(){
    return <div className={styles.wrapper}>

        <div className={styles.titre}>
            <h1 className={styles.competences}>Mes compétences</h1>
        </div>

        <div className={styles.pageContainer}>
        <Carte className={styles.carte} logo={csharp} titre="C Sharp"/>
        <Carte className={styles.carte} logo={java} titre="Java"/>
        <Carte className={styles.carte} logo={html} titre="HTML"/>
        <Carte className={styles.carte} logo={css} titre="CSS"/>
        <Carte className={styles.carte} logo={js} titre="JavaScript"/>
        <Carte className={styles.carte} logo={react} titre="React"/>
        <Carte className={styles.carte} logo={nodejs} titre="NodeJS"/>
        <Carte className={styles.carte} logo={sql} titre="SQL Server"/>
        </div>
        





    </div>



}