import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import { FaBriefcase, FaGraduationCap } from "react-icons/fa";
import { CgArrowLongUpR } from "react-icons/cg";
import styles from "./Apropos.module.css";

//https://www.npmjs.com/package/react-vertical-timeline-component   lien npmjs pour code pour le composant timeline en vertical

export default function Apropos() {
  return (
    <div className={styles.container}>
      <h1 className={styles.parcours}>Parcours</h1>
      <VerticalTimeline lineColor={"#ddd"}>
        
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          contentStyle={{ background: "rgb(233, 30, 99)", color: "#fff" }}
          contentArrowStyle={{ borderRight: "10px solid  rgb(233, 30, 99)" }}
          date="2022 - 20AA"
          dateClassName={styles.CouleurDate}
          iconStyle={{ background: "rgb(233, 30, 99)", color: "#fff" }}
          icon={<FaBriefcase />}
        >
          <h3 className="vertical-timeline-element-title">Développeur</h3>
          <h4 className="vertical-timeline-element-subtitle">Ottawa, ON</h4>
          <p>
          Nouveau diplômé, ouvert pour recrutement, prêt à rejoindre votre équipe afin d'accomplir des grandes choses.
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--education"
          contentStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          contentArrowStyle={{ borderRight: "10px solid  rgb(33, 150, 243)" }}
          date="2020 - 2022"
          dateClassName={styles.CouleurDate}
          iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          icon={<FaGraduationCap />}
        >
          <h3 className="vertical-timeline-element-title">
            DEC En programmation informatique
          </h3>
          <h4 className="vertical-timeline-element-subtitle">Diplôme DEC</h4>
          <p>Java, C#, SQL Server, Développement Web (HTML, CSS, JavaScript, NodeJS, ReactJS, Bootstrap)</p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--education"
          contentStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          contentArrowStyle={{ borderRight: "10px solid  rgb(33, 150, 243)" }}
          date="2017 - 2018"
          dateClassName={styles.CouleurDate}
          iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          icon={<FaGraduationCap />}
        >
          <h3 className="vertical-timeline-element-title">
            Master 1 Finance et commerce international
          </h3>
          <h4 className="vertical-timeline-element-subtitle">
            Niveau Master 1
          </h4>
          <p>Finance, Commerce, Gestion d'entreprise</p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--education"
          contentStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          contentArrowStyle={{ borderRight: "10px solid  rgb(33, 150, 243)" }}
          date="2014 - 2017"
          dateClassName={styles.CouleurDate}
          iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          icon={<FaGraduationCap />}
        >
          <h3 className="vertical-timeline-element-title">
            Baccalauréat en logistique et commerce international
          </h3>
          <h4 className="vertical-timeline-element-subtitle">
            Diplôme Baccalauréat
          </h4>
          <p>Commerce, Finance, Logistique</p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          iconStyle={{ background: "rgb(16, 204, 82)", color: "#fff" }}
          icon={<CgArrowLongUpR />}
        />
      </VerticalTimeline>
    </div>
  );
}
