import styles from "./introduction.module.css";
import portrait from "../../resources/portrait2.png";

export default function Introduction() {
  return (
    <div className={styles.i}>
      <div className={styles.i_gauche}>
        <div className={styles.i_gauche_wrapper}>
          <h2 className={styles.i_intro}>Salut, Mon nom est</h2>
          <h1 className={styles.i_nom}>Riyadh Redjradj</h1>
          <div className={styles.i_title}>
            <div className={styles.i_titre_wrapper}>
              <div className={styles.i_title_item}>développeur Web</div>
              <div className={styles.i_title_item}>Database développeur</div>
              <div className={styles.i_title_item}>développeur Java</div>
              <div className={styles.i_title_item}>développeur C#</div>
              <div className={styles.i_title_item}>vidéaste aérien</div>
            </div>
          </div>
          <div className={styles.i_description}>
            jJe suis nouveau diplome en programmation informatique, enthousiaste et motivé à travailler comme développeur au sein d’une équipe dynamique afin de mettre à profit mes compétences 
            en développement Web (HTML, CSS, JavaScript, NodeJS, ReactJS, Bootstrap) ainsi qu'en Java, C# et en SQL Server, je suis
            également photographe et vidéaste aérien amateur de la nature.
          </div>
        </div>
      </div>
      <div className={styles.i_droite}>
        <div className={styles.i_bg}></div>
        <img src={portrait} className={styles.i_img} alt="portrait Riad" />
      </div>
    </div>
  );
}
